import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import {
    getFromStorage,
    setInStorage
} from '../utils/storage'

const Navbar = () => {

    const onLogout = () => {
        console.log('onlogout')
        this.setState({
            isLoading: true
        })
        let obj = getFromStorage('speak_your_mind')

        if (obj && obj.token) {
            let token = obj.token
            console.log('a')

            fetch('/api/account/logout?token=' + token)
                .then(res => res.json())
                .then(json => {
                    if (json.success) {
                        console.log('success')
                        this.setState({
                            token: '',
                            isLoading: false
                        })
                    } else {
                        console.log('here ')
                        this.setState({
                            isLoading: false
                        })
                    }
                })
        } else {
            console.log('ahhh')
            this.setState({
                isLoading: false
            })
        }
    }

    return (
        <nav className="navbar navbar-dark bg-dark navbar-expand-lg">
            <Link to="/" className="navbar-brand">a</Link>
            <div className="collpase navbar-collapse">
                <ul className="navbar-nav mr-auto">
                    <li className="navbar-item">
                        <Link to="/" className="nav-link">b</Link>
                    </li>
                    <li className="navbar-item">
                        <Link to="/create" className="nav-link">c</Link>
                    </li>
                    <li className="navbar-item">
                        {/* <Link to="/user" className="nav-link">logout</Link> */}
                        <button onClick={() => onLogout()}>Logout</button>
                    </li>
                </ul>
            </div>
        </nav>
    );

}

export default Navbar